import React from 'react';
import PropTypes from 'prop-types';
import { graphql, compose } from 'react-apollo';
import withStyles from 'isomorphic-style-loader/lib/withStyles';
import newsQuery from './news.graphql';
import s from './Home.css';

class Home extends React.Component {
  static propTypes = {
    data: PropTypes.shape({
      loading: PropTypes.bool.isRequired,
      news: PropTypes.arrayOf(
        PropTypes.shape({
          title: PropTypes.string.isRequired,
          link: PropTypes.string.isRequired,
          content: PropTypes.string,
        }),
      ),
    }).isRequired,
  }

  static contextTypes = {
    searchString: PropTypes.string
  }

  loadProfile = (input) => {
    const item = document.getElementById(input)
    item.className += " " + "hover"
    for (var i = item.children.length - 1; i >= 0; i--) {
      if(item.children[i].className == "newsDesc") {
                item.setAttribute('visibility', 'visible')
      }
    }

      //set new page
      console.log('load Profile ', input)
  }

  render() {
    console.log('context value ', this.context.searchString)
    var searchString = this.context.searchString
    const { data: { loading, databaseGetAllCompanies } } = this.props;
    return (
      <div className={s.root}>
        <div className={s.container}>
          <br />
          {loading
            ? 'Loading...'
            : databaseGetAllCompanies.filter(item => item.name.includes("")).map(item => (
                <article id={item.id} key={item.id} onClick={this.loadProfile.bind(this, item.id)} className={s.newsItem}>
                  <h1 className={s.newsTitle}>{item.name}</h1>
                  <div className={s.newsDesc}>
                    City: {item.region != '0' ? item.region : ' '}
                  </div>
                  <div className={s.newsDesc}>
                    Industry: {item.industry != '0' ? item.industry : ' '}
                  </div>
                  <div className={s.newsDesc}>
                    Size: {item.size != '0' ? item.size : ' '}
                  </div>
                  <div className={s.newsDesc}>
                    CEO: {item.ceoName != '0' ? item.ceoName : ' '}
                  </div>
                  <div className={s.newsDesc}>
                    <a href={item.linkedin}>Linkedin</a>
                  </div>
                  <div className={s.newsDesc}>
                    <a href={item.website}>Website</a>
                  </div>
                </article>
              ))}
        </div>
      </div>
    );
  }
}

export default compose(withStyles(s), graphql(newsQuery))(Home);
